.wishes-section {
	position: relative;
	padding: 5rem 0 0;
	@include max-md {
		padding: 3rem 0 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		right: 0;
		width: 200px;
		transform: translate(10%, -30%) scaleX(-1);
		@include max-lg {
			width: 160px;
		}
		@include max-md {
			width: 120px;
			transform: translate(10%, -50%) scaleX(-1);
		}
	}
	.orn-2 {
		position: absolute;
		bottom: 0;
		left: 0;
		height: 90%;
		transform: translateX(-50%);
		@include max-md {
			transform: translateX(-15%);
		}
	}
	.image-wrap {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		.wishes-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	.card-form {
		margin-bottom: 0;
		background-color: var(--color-primary);
		position: relative;
		z-index: 3;
		border-radius: 10px;
		&.card {
			border: none;
		}
		.card-body {
			@include min-lg {
				padding: 2rem;
			}
		}
	}

	.wishes-preview {
		margin-top: -10rem;
		padding-top: 10rem;
		position: relative;
		&::before {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background-color: var(--color-secondary);
			opacity: 0.4;
			content: "";
		}
		.card {
			border: none;
			position: relative;
			background-color: transparent;
			.card-body {
				overflow: hidden;
				@include min-lg {
					padding: 2rem;
				}
				.wishes {
					text-align: left;
					height: 23.05rem;
					overflow: auto;
					&::-webkit-scrollbar {
						display: none;
					}
					// .text-muted {
					// 	color: rgba($color: #fff, $alpha: 0.8) !important;
					// }
					.wish {
						margin-bottom: 1.5rem;
						h6 {
							font-weight: bold;
						}
						h6,
						p {
							margin-bottom: 0;
						}
					}
				}
			}
		}
	}
}
