.logo-viding {
	height: 2.4rem;
	@include max-md {
		height: 2.25rem;
	}
	path {
		fill: #fff;
	}
}

.header-section {
	position: relative;
	height: 100vh;
	width: 100%;
	&::after {
		position: absolute;
		width: 100%;
		height: 30%;
		background: linear-gradient(to top, #fff 0%, transparent 100%);
		content: "";
		bottom: 0;
		left: 0;
	}
	.orn-1 {
		position: absolute;
		left: 0;
		bottom: 0;
		width: 80px;
		z-index: 3;
		@include max-lg {
			width: 60px;
		}
		@include max-md {
			width: 40px;
		}
	}
	.orn-2 {
		position: absolute;
		right: 0;
		top: 0;
		transform: scaleX(-1);
		width: 80px;
		@include max-lg {
			width: 60px;
		}
		@include max-md {
			width: 40px;
		}
	}
	.image-wrapper {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		&::after {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background: rgba($color: #000000, $alpha: 0.4);
			content: "";
		}
		.background-header {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	.header-content {
		position: relative;
		text-align: center;
		padding: 4rem 0 0;
		.header {
			text-align: center;
			width: 100%;
			height: 100%;
			display: flex;
			flex-direction: column;
			justify-content: flex-start;
			align-items: center;
			padding: 1.5rem 0 3rem;
			position: relative;
			z-index: 3;
			.header-title {
				color: #fff;
				position: relative;
				.title {
					color: #fff;
				}
			}
		}
	}
}

.envelope-wrap {
	position: fixed;
	z-index: 1300;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	color: #fff;
	.header-content {
		.header {
			justify-content: space-between;
			.header-title {
				margin-bottom: 0;
				padding: 3rem;
				@include max-md {
					padding: 3rem 0.75rem;
				}
			}
		}
	}
}
