.apology-section {
	position: relative;
	padding: 5rem 0;
	background-color: #fff;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		left: 0;
		top: 0;
		width: 80px;
		transform: translateX(-40%);
	}
	.orn-2 {
		position: absolute;
		right: 0;
		bottom: 0;
		width: 180px;
		transform: translateX(70%);
		@include max-lg {
			width: 150px;
		}
	}
	.appology-wrap {
		position: relative;
		border-radius: 10px;
		background-color: var(--color-primary);
		padding: 3rem 2rem;
		@include max-md {
			padding: 2rem 1rem;
		}
	}
}
