.story-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 10%;
		height: 120px;
		transform: translateY(50%);
		@include max-lg {
			height: 100px;
		}
		@include max-md {
			height: 80px;
		}
	}
	.story-image-abs {
		margin-bottom: -3rem;
		@include min-md {
			position: absolute;
			left: 0;
			margin-bottom: 0;
		}
	}
	.story-description {
		position: relative;
		text-align: center;
		background-color: var(--color-primary);
		padding: 3rem 2rem;
		z-index: 3;
		border-radius: 10px;
		@include max-md {
			padding: 2rem 1.25rem;
		}
	}
	.image-story-wrap {
		position: relative;
		height: 480px;
		width: 100%;
		@include max-md {
			height: 380px;
		}
		&::before {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background-color: rgba($color: #000000, $alpha: 0.3);
			content: "";
		}
		.story-image {
			object-fit: cover;
			width: 100%;
			height: 100%;
		}
	}
}
