.thank-section {
	position: relative;
	padding: 3rem 0;
	text-align: center;
	color: #ffff;
	&::before {
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;
		background-color: rgba($color: #000000, $alpha: 0.4);
		content: "";
		z-index: 2;
	}
	.orn-1 {
		position: absolute;
		left: 0;
		bottom: 0;
		width: 70px;
		z-index: 3;
		@include max-lg {
			width: 60px;
		}
		@include max-md {
			width: 40px;
		}
	}
	.thank-background {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
	.thank {
		position: relative;
		text-align: center;
		padding: 5rem 2rem;
		z-index: 3;
		@include max-md {
			border-width: 2px;
			padding: 2rem 0.75rem;
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	small {
		position: relative;
	}
}
