.venue-section {
	position: relative;
	padding: 5rem 0;
	background-color: var(--color-primary);
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 280px;
		transform: translateY(-40%);
		@include max-md {
			width: 240px;
			transform: translateY(-35%);
		}
	}
	.orn-2 {
		position: absolute;
		right: 0;
		top: 30px;
		width: 280px;
		transform: scaleX(-1) translateX(-30%);
		@include max-md {
			transform: scaleX(-1) translateX(-20%);
		}
	}
	.venue-wrapper {
		position: relative;
		.venue-content {
			position: relative;
			text-align: center;
			.card {
				border: none;
				background-color: var(--color-main);
				height: 100%;
				border-radius: 10px;
				.card-body {
					padding: 3rem 2rem;
					@include max-md {
						padding: 2rem 1rem;
					}
				}
			}
		}
	}
	.prokes-wrap {
		display: flex;
		gap: 0.75rem;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		text-align: center;
		.prokes {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 132px;
			@include max-md {
				width: 120px;
			}
		}
	}
	.row-venue {
		gap: 3rem;
		&-sm {
			@include max-md {
				gap: 3rem;
			}
		}
	}
	.event-name {
		position: relative;
		.date {
			display: flex;
			align-items: center;
			gap: 0.5rem;
			text-align: left;
			h5 {
				font-weight: 700;
				margin-bottom: 0;
				font-size: 38px;
			}
			p {
				margin-top: 0.2rem;
				font-size: 12px;
			}
		}
		p {
			margin-bottom: 0;
		}
	}
	.btn-custom {
		background-color: transparent;
		border-color: var(--color-dark);
		color: var(--color-dark);
		transition: all 0.25 ease-in-out;
		&:hover {
			background-color: var(--color-secondary);
			border-color: var(--color-secondary);
			color: #fff;
		}
	}
	.maps-embed {
		width: 100%;
		position: relative;
	}
}
