.couple-section {
	position: relative;
	padding: 0 0 5rem;
	text-align: center;
	@include max-md {
		padding: 0 0 3rem;
	}
	.and {
		@include min-md {
			position: absolute;
			top: 40%;
			left: 50%;
			transform: translate(-50%, -40%);
		}
		margin: 1rem 0;
		font-weight: 600;
		color: var(--color-dark);
	}
	.couple {
		position: relative;
		.image-wrap {
			width: 100%;
			margin: auto;
			position: relative;
			margin-bottom: 1.25rem;
			height: 320px;
			@include max-md {
				width: 80%;
				height: 280px;
			}
			.couple-image {
				height: 100%;
				object-fit: cover;
				position: relative;
				width: 100%;
			}
		}
		.couple-parent {
			width: 80%;
			margin: auto;
		}
		.sosmed-wrap {
			position: relative;
			width: 100%;
			.sosmed {
				color: var(--color-dark);
				text-decoration: none;
				small {
					align-items: center;
					gap: 0.25rem;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
