.gallery-section {
	position: relative;
	padding: 3rem 0;
	.img-gallery {
		height: 480px;
		width: 100%;
		object-fit: cover;
		@include max-lg {
			height: 420px;
		}
		@include max-md {
			height: 320px;
		}
	}
	.img-thumbnail {
		object-fit: cover;
		padding: 0;
		border: none;
		border-radius: 0;
		width: 100%;
		height: 100%;
	}
	.splide {
		.splide__slide.is-active {
			.img-thumbnail {
				border: 2px solid #fff;
			}
		}
		.splide__arrow {
			border-radius: 0;
			width: 70px;
			height: 65px;
			background-color: var(--color-secondary);
			@include max-lg {
				width: 40px;
				height: 35px;
			}
			svg {
				fill: #fff;
				width: 25px;
				height: 30px;
				@include max-md {
					width: 15px;
					height: 20px;
				}
			}
			&--prev {
				left: 0;
			}
			&--next {
				right: 0;
			}
		}
	}
}
