.stream-section {
	position: relative;
	padding: 3rem 0;
	text-align: center;
	@include max-lg {
		.row {
			gap: 1.5rem;
		}
	}
	.stream-wrapper {
		position: relative;
		background-color: var(--color-primary);
		padding: 3rem 2rem;
		border-radius: 10px;
		height: 100%;
		@include max-md {
			padding: 2.5rem 1rem;
		}
		h4 {
			@include max-md {
				text-align: center;
			}
		}
	}
	ul {
		ol {
			padding: 0;
		}
	}
	.card {
		background-color: transparent;
		height: 100%;
		color: var(--color-dark);
		border: none;
	}
	.btn-custom {
		width: 50%;
		@include max-lg {
			width: 100%;
		}
		@include max-md {
			width: fit-content;
		}
	}
	.yt-embed {
		@include min-lg {
			height: 320px;
		}
	}
	.yt-description {
		width: 100%;
		margin-top: 3rem;
		@include max-md {
			text-align: center;
		}
	}
}
